.Home {
  position: relative;
  top: 100px;
  display: flex;
  justify-content: center;
  height: calc(100vh - 100px);
}

.container {
  position: relative;
  width: 100%;
  font-family: var(--primary-font);
}

.imgContainer {
  position: absolute;
  width: 100%;
}

.image {
  width: 100%;
}

.headingContainer {
  position: absolute;
  top: 70%;
  padding: 1rem;
}

.headingContainer h1 {
  color: var(--primary-black);
  font-size: 3rem;
}

.headingContainer h2 {
  color: var(--primary-blue);
  font-size: 3rem;
}

.text {
  position: absolute;
  padding: 1.5rem;
  top: 120%;
  color: var(--primary-gray);
  font-size: 1.5rem;
}

.grey {
  height: 50%;
  width: 100%;
  background-color: #f2f2f2;
  position: absolute;
  bottom: 0;
  z-index: -100;
  display: none;
}

.PageNo {
  font-size: 4.5rem;
  color: var(--primary-blue);
  display: none;
}

.PageNo h1 {
  display: inline;
  border-bottom: 8px solid #f2f2f2;
}

.title {
  position: absolute;
  display: none;
  left: 7rem;
  top: 25%;
}

.MobileDesigner {
  position: absolute;
  top: 260%;
  right: 5%;
}

.MobileDesigner a {
  color: var(--primary-blue);
}

.designer {
  position: absolute;
  margin-top: 1rem;
  left: 80%;
}

.designer a {
  color: var(--primary-blue);
}

.contactUs {
  position: relative;
  left: 5%;
}

.contactUsMobile {
  position: absolute;
  left: 8%;
  bottom: -130%;
}

.circleOne {
  width: 60px;
  height: 60px;
  border: 10px solid var(--primary-yellow);
  border-radius: 50%;
  opacity: 0.5;
  position: absolute;
  left: 40%;
  top: 0;
}

.circleTwo {
  width: 40px;
  height: 40px;
  border: 10px solid var(--primary-yellow);
  border-radius: 50%;
  opacity: 0.5;
  position: absolute;
  left: 70%;
  top: 40%;
}

.circleThree {
  width: 160px;
  height: 160px;
  border: 10px solid var(--primary-yellow);
  border-radius: 50%;
  opacity: 0.5;
  position: absolute;
  left: -16%;
  top: 60%;
}

@media (max-width: 320px) {
  .MobileDesigner {
    top: 270%;
  }
  .contactUsMobile {
    bottom: -150%;
  }
}

@media (min-width: 400px) {
  .contactUsMobile {
    bottom: -110%;
  }
}

@media (min-width: 540px) {
  .headingContainer h1 {
    font-size: 3.5rem;
  }
  .headingContainer h2 {
    font-size: 3.5rem;
  }
  .text {
    top: 110%;
    font-size: 1.8rem;
  }
  .MobileDesigner {
    top: 200%;
  }
  .contactUsMobile {
    bottom: -80%;
  }
  .circleTwo {
    top: 65%;
  }
}

@media (min-width: 768px) {
  .container {
    width: 768px;
  }
  .headingContainer {
    top: 80%;
  }
  .headingContainer h1 {
    font-size: 4rem;
  }
  .headingContainer h2 {
    font-size: 4rem;
  }
  .text {
    font-size: 2rem;
  }
  .MobileDesigner {
    top: 170%;
  }
  .contactUsMobile {
    bottom: -55%;
  }
  .circleTwo {
    top: 75%;
  }
  .circleThree {
    left: -10%;
  }
}

@media (min-width: 992px) {
  .Home {
    top: 120px;
    height: calc(100vh - 120px);
  }
  .container {
    width: 992px;
  }
  .imgContainer {
    right: 0;
    width: 500px;
    height: 350px;
  }
  .headingContainer {
    display: none;
  }
  .text {
    display: none;
  }
  .MobileDesigner {
    display: none;
  }
  .grey {
    display: block;
  }
  .PageNo {
    display: block;
  }
  .title {
    display: block;
  }
  .title h1 {
    font-size: 6rem;
    color: var(--primary-black);
  }

  .title h2 {
    font-size: 6rem;
    color: var(--primary-blue);
  }
  .para {
    padding: 1rem;
    font-size: 2rem;
    color: var(--primary-gray);
  }
  .image {
    border-radius: 15%;
  }
  .contactUsMobile {
    display: none;
  }
  .circleTwo {
    top: 60%;
  }
  .circleThree {
    left: -5%;
  }
}

@media (min-width: 1200px) {
  .Home {
    top: 140px;
    height: calc(100vh - 140px);
  }
  .container {
    width: 1200px;
  }
  .imgContainer {
    right: 0;
    width: 600px;
    height: 450px;
  }
  .title h1 {
    font-size: 7rem;
    color: var(--primary-black);
  }

  .title h2 {
    font-size: 7rem;
    color: var(--primary-blue);
  }
}
